<template>
<div id="home">
  <div class="header bg-light row">
  <div class="logo pl pr" >
    <img width="29" src="data:image/png;base64," alt="">
  </div>
    <input type="text" class="col">
    <div class="user pl pr">
      <img width="29" src="data:image/png;base64," alt="">
    </div>
  </div>
  <div class="sub-header bg-light">
      <div class="scroll">
          <span class="slider" v-for="item in tabs" :key="item.name">{{item.name}}</span>
      </div>
    </div>


  <div class="content has-header has-sub-header has-footer">
    我是首页内容
  </div>

</div>

</template>

<script>
  import {Swiper} from "@/components/swiper.js"
   export default {
     name:"home",
     data(){
       return { tabs:[]}
     },
     created(){
       this.$http.get("https://m.mi.com/v1/home/page")
         .then((res)=>{
            this.tabs=res.data.data.tabs;
           console.log(res)
         },(err)=>{})
     },
     //组件已经渲染完毕
     mounted(){
     //  swiper 能用
       var elem =document.querySelector(".sub-header .scroll");
       setTimeout(()=>{
         // loop: 新建第最后一个插入到前面，第一个插入到后面
         // bounce 是否为弹性动画
         // page 是不是幻灯片
         this.scroller=new Swiper(elem,{loop:false,bounce:false,page:false})
       },80)
     }

   }
</script>

<style >
.scroll .slider{
  padding-left: 15px;
  padding-right: 15px;
  float: left;
  line-height: 43px;
}

</style>



