// 01  导入vue 和 vue router
import Vue from 'vue'
import Router from 'vue-router'
// 02  导入页面需要的组件
import home from '@/page/home/home.vue'
import category from '@/page/category/category.vue'
import detail from '@/page/detail/detail.vue'
import des from '@/page/detail/des.vue'
import arg from '@/page/detail/arg.vue'
// 03  Vue 使用路由
Vue.use(Router)

// 04 导出并配置路由
export default new Router({
  routes: [
    {
      path: '/',        //  如果为/ 默认跳转到home
      redirect:"/home"
    },
    {
      path: '/home',        // 路由的地址
      name: 'home',     // 路由的名称
      component: home,   // 路由需要的组件
      alias:"/index"
    },
    {
      path: '/category',
      name: 'category',
      component: category
    },
    // 动态路由
    {
      path: '/detail/:id',
      name: 'detail',
      component: detail,
    // children
      children:[
        {path: '', redirect: 'des'},//如果子路由是空的 跳转到 des
        {path:"des",
          component:des
        },
        {path:"arg",
          component:arg
        }
      ]
    }
  ]
})



