<template>
  <div id="app">

    <!-- 06 router-view 存放路由页面的-->
     <router-view/>
    <div class="tabs row text-center row-center bg-light">
      <!--07 路由导航的  to 要和路由配置路径里面的path一样-->

      <router-link to="/home" class="col" >首页</router-link>
      <router-link to="/category" class="col" >分类</router-link>
      <router-link to="/detail/abc" class="col" >购物车</router-link>
      <router-link to="/detail/cdf" class="col" >我的</router-link>


    </div>
  </div>
</template>

<script>
import custom from "./components/custom.vue"
import my  from "@/components/my"
export default {
  name: 'App',
  components:{}

}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
  font-size: 14px;

}
a{
  color: #999;
  text-decoration: none;
}
a:hover,.router-link-active{
  color:#ff6700;
}
img{ vertical-align: middle}

/*通用*/
  /*文字*/
  .text-center{ text-align: center}
/*颜色*/
  .bg-light{ background-color:#f2f2f2; }
/*栅格*/
  .row{
    display: flex;
    width: 100%;
    line-height: 43px;
  }
  .col{ flex: 1;}
  .row-center{
    justify-content: center;
  }
  .padding{ padding: 10px;}
  .pl{
    padding-left: 10px;}
  .pr{
    padding-right: 10px;}

/*组件*/

  /*header*/
  .header{
    height: 43px;
    width: 100%;
    }
  .header{ height: 43px;}
  .header input[type=text]{
    height: 29px;
    padding: 0 5px;
    border: none;
    margin-top: 7px;
  }
  .sub-header{ height: 43px;}
  .content{
    position: absolute;
    top:0; left:0; right:0; bottom:0;
  }
  .has-header.content,.has-sub-header.content{ top:43px}
  .has-header.has-sub-header.content{top:86px;}
  .has-footer.content{ bottom:49px;}
  /*tabs*/
  .tabs{
    height: 49px;
    position:fixed;
    left:0;
    bottom:0;
    width: 100%;}
  .tabs a{ line-height: 49px;}

.router-link-active{color:red;}
  /* 43 在ios 里面一个列表的高度   29一个列表图标的高度  15 间距   49tabs*/
</style>



